/* Media Query */

@media only screen and (max-width: 991px) {
  .homepage {
    .rect-nav {
      display: none;
    }
    .Line-1, .Line-2 {
      width: 29.5%;
    }
    .best-title {
      width: 39.5%;
    }
    .best-frames .slick-arrow, .best-frames .slick-arrow:hover, .best-frames .slick-arrow:focus {
      background: #f5a623;
      color: #fff;
    }
    .best-frames .slick-prev {
      left: 0;
      z-index: 999;
    }
    .best-frames .slick-next {
      right: 0;
      z-index: 999;
    }
    .best-frames .slick-prev:before {
      content: "\f104";
    }
    .best-frames .slick-next:before {
      content: "\f105";
    }
    .best-frames .slick-prev:before, .best-frames .slick-next:before  {
      font-family: FontAwesome;
      color: #fff;
      font-size: 30px;
    }
    .best-frames .slick-prev, .best-frames .slick-next {
      width: 30px;
      height: 30px;
      height: 35px;
    }
    .tbb-title {
      display: none;
    }
    .tbb-title-tab {
      display: block;
      .Line-1, .Line-2 {
        width: 20%;
      }
      .best-title {
        width: 58.5%;
      }
    }
    .the-brand-boutique {
      margin-top: 60px;
    }
    .tbb-frames .frame-1 {
      height: 479px;
    }
    .tbb-frames .frame-2, .frame-2 {
      height: 225.5px;
    }
    .computer-column {
      display: none !important;
    }
    .tbb-frames .sixteen.wide.tablet.column .frame-2 {
      height: 244px;
    }
    .tablet-column {
      display: block !important;
    }
    .ui.column.grid>[class*="ten wide tablet"].column, .ui.grid>.column.row>[class*="ten wide tablet"].column, .ui.grid>.row>[class*="ten wide tablet"].column, .ui.grid>[class*="ten wide tablet"].column {
      width: 510px!important;
    }
    .ui.column.grid>[class*="six wide"].column, .ui.grid>.column.row>[class*="six wide"].column, .ui.grid>.row>[class*="six wide"].column, .ui.grid>[class*="six wide"].column {
      width: 255px!important;
    }
    .tbb-text-01 {
      top: 71px;
      right: 24px;
    }
    .rectangle-5 {
      margin-top: 17px;
      padding-bottom: 79px;
    }
    .rectangle-5 .best-title {
      width: 45.5%;
    }
    .rectangle-5 .Line-1, .rectangle-5 .Line-2 {
      width: 26.5%;
    }
    .rectangle-5 .ui.grid {
      margin-top: 27px;
    }
    .rectangle-8 {
      width: 100%;
    }
    #responsive-js-1 .article-column {
      margin-bottom: 28px;
    }
    .rectangle-6 .container {
      display: block;
      text-align: center;
      .r6-text-01, .r6-text-02 {
        text-align: center;
      }
      .r6-text-01 {
        margin-bottom: 7px;
      }
      .r6-text-02 {
        margin-bottom: 19px;
      }
    }
  }
}

